@import url(https://fonts.googleapis.com/css2?family=Stylish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainBox{padding:10px;background-color:rgba(200,200,200,0.1);box-shadow:0 5px 15px rgba(0,0,0,0.5);border-radius:10px;font-family:"Stylish", sans-serif;text-align:center}.mainBox input{margin:auto;width:80%;height:30px;background:transparent;color:#acffef;font-weight:bold;font-size:large}.mainBox input:focus{outline:none}.mainBox button{margin:auto;height:37px;background:transparent;color:#acffef;font-weight:bold;font-size:large}.mainBox button:focus{outline:none}.mainBox h2{text-align:center;position:relative;margin:0 0 20px;padding:0;font-size:48px;text-transform:uppercase;z-index:2;color:#acffef}.mainBox p{font-size:1rem;color:#acffef}.mainBox .content{font-size:1.2rem}.mainBox a{color:#acffef}.mainBox a:link{color:#acffef;text-decoration:none}.mainBox a:visited{color:#acffef}.notMain h2{font-size:1rem;margin:0}.notMain p{display:none}@media screen and (max-width: 576px){.notMain h2{font-size:1rem;padding-right:10px}}

.Circle{width:3px;height:3px;background-color:red;border-radius:50%;opacity:1;-webkit-animation-name:example;animation-name:example;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}.Circle2{width:3px;height:3px;background-color:lime;border-radius:50%;opacity:1;-webkit-animation-name:example;animation-name:example;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes example{0%{transform:scale(0);opacity:1;transform-origin:center}100%{transform:scale(4.5);opacity:0;transform-origin:center}}@keyframes example{0%{transform:scale(0);opacity:1;transform-origin:center}100%{transform:scale(4.5);opacity:0;transform-origin:center}}

.footer-container{position:absolute;bottom:0;color:white;background-color:black;text-align:center;justify-content:center;width:100%;height:15px;padding-bottom:10px;z-index:3;font-family:'Stylish', sans-serif}

#menuicon{display:none}#menuicon+label{display:block;width:40px;height:30px;position:fixed;right:0;top:10%;cursor:pointer}#menuicon+label span{display:block;position:absolute;width:100%;height:5px;border-radius:30px;background:#ACFFEF;transition:all 0.35s}#menuicon+label span:nth-child(1){top:0}#menuicon+label span:nth-child(2){top:50%;transform:translateY(-50%)}#menuicon+label span:nth-child(3){bottom:0}#menuicon:checked+label{z-index:2;right:300px}#menuicon:checked+label span:nth-child(1){top:50%;transform:translateY(-50%) rotate(45deg)}#menuicon:checked+label span:nth-child(2){opacity:0}#menuicon:checked+label span:nth-child(3){bottom:50%;transform:translateY(50%) rotate(-45deg)}.checkbox{z-index:2}.sidebar{width:300px;height:79%;background-color:rgba(200,200,200,0.1);position:fixed;top:10%;right:-300px;z-index:1;transition:all 0.35s}#menuicon:checked+label+div{right:0}

.userInput-list-template {
  background-color: rgba(200, 200, 200, .1);
  /* width: 512px; */
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); /* 그림자 */
  margin: 0 auto; /* 페이지 중앙 정렬 */
}

.form-wrapper {
  padding: 1rem;
  border-bottom: 1px solid #22b8cf;
}

.userInput-wrapper {
  min-height: 5rem;
}

.form {
  display: flex;
  align-items: center;
}

.form input {
  flex: 1 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
  height: 40px;
  font-size: 1.25rem;
  outline: none;
  border: none;
  color: #22b8cf;
  border-bottom: 1px solid #c5f6fa;
  background-color: rgba(200, 200, 200, .1);
}

.create-button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: 1rem;
  background: #22b8cf;
  border-radius: 3px;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.create-button:hover {
  background: #3bc9db;
}

.userInput-item {
  padding: 1rem;
  display: flex;
  align-items: center; /* 세로 가운데 정렬 */
  cursor: pointer;
  transition: all 0.15s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.userInput-item:hover {
  background: rgba(200, 200, 200, .3);
}

/* userInput-item 에 마우스가 있을때만 .remove 보이기 */
.userInput-item:hover .remove {
  opacity: 1;
}

/* userInput-item 사이에 윗 테두리 */
.userInput-item + .todo-item {
  border-top: 1px solid #f1f3f5;
}

.remove {
  margin-right: 1rem;
  color: #e64980;
  font-weight: 600;
  opacity: 0;
}

.userInput-text {
  flex: 1 1; /* 체크, 엑스를 제외한 공간 다 채우기 */
  word-break: break-all;
  color: #acffef;
  font-size: 1.2em;
}

.checked {
  text-decoration: line-through;
  color: #adb5bd;
}

.check-mark {
  font-size: 1.5rem;
  line-height: 1rem;
  margin-left: 1rem;
  color: #3bc9db;
  font-weight: 800;
}

.mainForm{display:absolute;font-family:'Stylish', sans-serif}.mainForm h2{margin-left:110px;margin-top:20px;color:#acffef}.mainForm p{margin-left:70px;color:#acffef;font-size:1.2em}.mainForm .userInputSubmit{font-family:'Stylish', sans-serif;background:transparent;margin-left:120px;margin-top:10px;padding:10px 10px;color:#acffef}

.container{display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(12, 1fr);grid-gap:20px;grid-gap:20px;gap:20px}.item .isMain{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)}.item .notMain{top:10%;left:20%;width:40vw;height:45px;padding:5px;display:flex;align-items:center}@media screen and (max-width: 576px){.item .isMain{position:absolute;top:25%;left:0%;transform:translate(0%, 0%)}.item .notMain{display:flex;justify-content:center;align-items:center;height:35px;padding:10px;width:70vw}}

.App {
  text-align: center;
}

